.post {
  display: flex;

  margin-bottom: 30px;
}

.post__user {
  width: 20%;
  margin: 20px;
  padding: 10px;

  border: 2px solid black;
  border-radius: 20px;
}

.post__info {
  width: 80%;
  margin: 0 15px;
  padding: 15px;

  border: 5px solid black;
  border-radius: 20px;
  color: #120824;
}

.post__title {
  font-size: 30px;
}

.post-btn {
  width: 15%;
  height: 30px;
  color: #fff;
  background-color: black;
  margin: auto;

  cursor: pointer;
}
